.checkbox--disabled:hover .radio__icon,
.radio--disabled:hover .checkbox__icon,
.radio--disabled:hover .radio__icon {
  border-color: #8f8f8f;
}

.notice {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  transform: translateY(-150px);
  color: #fff;
  text-align: center;
  font-size: 1rem;
  padding: 10px;
  z-index: 99999;
  transition: all 0.3s linear;
  opacity: 0;
}

.notice--open {
  transform: none;
  opacity: 1;
}

.notice--green {
  background: #28a745;
  box-shadow: 0 5px 10px rgba(40, 167, 69, 0.2);
}

.notice--red {
  background: #dc3545;
  box-shadow: 0 5px 10px rgba(220, 53, 69, 0.2);
}

@keyframes errorInput {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(-6px);
  }
  100% {
    transform: translateX(6px);
  }
}

.field__error-message {
  color: #dc3545;
  font-size: 14px;
  margin: 10px 0 0 22px;
}

.field-name {
  font-weight: 700;
  font-size: 14px;
  line-height: 1;
  display: block;
  margin: 0 0 20px 20px;
}

.field,
.datepicker-module input {
  border: solid 2px #8f8f8f;
  background: #fff;
  padding-left: 20px;
  width: 100%;
  max-width: 350px;
  display: inline-block;
  font-weight: 300;
  font-family: "TimesNewRoman";
  font-size: 14px;
  font-weight: 400;
  transition: border 0.3s ease;
  color: #8f8f8f;
}

.field--error,
.datepicker-module input--error {
  border-color: #dc3545 !important;
  animation: errorInput 0.2s ease;
}

.field--disabled,
.datepicker-module input--disabled {
  cursor: not-allowed;
  background: #8f8f8f;
  pointer-events: none;
}

.field::placeholder,
.datepicker-module input::placeholder {
  color: #bdbdbd;
  font-family: "TimesNewRoman";
}

.field:hover,
.datepicker-module input:hover {
  border-color: #dedbdb;
}

.field:focus, .field.focus,
.datepicker-module input:focus,
.datepicker-module input.focus {
  border-color: #dedbdb;
}

.field::-ms-clear,
.datepicker-module input::-ms-clear {
  display: none !important;
}

textarea {
  min-height: 100px;
  resize: vertical;
  padding-top: 10px;
  width: 100%;
  max-width: 450px;
}

.field-row {
  margin-bottom: 35px;
  position: relative;
}

.field-row__name {
  font-weight: 700;
  font-size: 14px;
  margin-bottom: 15px;
  display: block;
  padding-left: 20px;
}

.field-row input,
.field-row textarea {
  width: 100%;
  max-width: 100%;
}

.field-row textarea {
  height: 120px;
}

@media (max-width: 575.98px) {
  .field-row {
    margin-bottom: 20px;
  }
}

.dva {
  background: linear-gradient(90deg, #be801e 0%, #be801e 1%, #fcd07c 100%);
}
